<template>
  <div>
    <!-- view -->
    <div class="simul-view">
      <img src="@/assets/images/bg-alex.png" alt="" class="bg">
      <div class="boss-position">
        <img class="boss" 
          v-for="(boss,index) in yourBoss" :key="index"
