        </li>
      </ul>
    </div>
    <!-- intro -->
    <transition name="fade">
      <div class="intro" v-if="isIntro">
        <!-- setting -->
        <div class="inner" v-if="isSetting">
          <h1>Soma Simulation</h1>
          <p>
           3体最初・時間停止のギミックで、場合による位置を確認・復習するサイトです。
          </p>
          <!-- <label> -->
            <!-- <span>Timer</span> -->
