<script>
export default {
  name: 'soma',
  data(){
    return{
      boss:[{
        name:'justice',
        url:require('@/assets/images/img-justice.png')
      },{
        name:'chaser',
        url:require('@/assets/images/img-chaser.png')
      }
     ],
      buff:[{
            url:require('@/assets/images/buff1.png')
          },{
              url:require('@/assets/images/buff2.png')
          },{
              url:require('@/assets/images/buff3.png')
          },{
              url:require('@/assets/images/buff4.png')
          }
      ],
      yourBuff:'소마소마',
      yourBoss:[],
      isIntro:true,
      isSetting:true,
      time:3,
      timer:0,
    }
  },
  methods:{
    getResult(){
      // buff
      this.yourBuff = this.buff[(this.getRandom(this.buff.length))];

      //boss
      this.yourBoss = [];
      let boss1 = this.boss[(this.getRandom(this.boss.length))];
      let boss2;
      if(boss1.name == this.boss[0].name){
        boss2 = this.boss[1];
      }else{
        boss2 = this.boss[0]
      }
      this.yourBoss.push(boss1);
      this.yourBoss.push(boss2);
    },
    getRandom(max){
        return Math.floor(Math.random() * Math.floor(max));
    },
    start(){
      this.isIntro = false;
      setTimeout(this.getResult(),1000);
    }
    // start(){
